html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
menu {
  margin: 0;
  padding: 0; }

body {
  min-width: 320px;
  overflow-x: hidden;
  color: #1B1C21;
  font-family: "TT Lakes Regular", sans-serif;
  background-color: #F8F8F8;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px; }

.container {
  padding: 0 16px; }

button {
  background: none;
  border: none;
  cursor: pointer; }

summary {
  cursor: pointer; }

@media screen and (min-width: 768px) {
  .container {
    padding: 0 24px; } }

@media screen and (min-width: 1120px) {
  .container {
    padding: 0 32px; }
    .container--1120 {
      width: 1120px;
      margin-right: 0;
      margin-left: auto; } }

.nav-menu {
  position: fixed;
  width: 320px;
  height: 100vh;
  padding: 18px 16px 32px;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
  -ms-overflow-style: none;
  overflow-y: scroll;
  flex-direction: column;
  z-index: 10;
  display: none;
  background: #ffffff; }
  .nav-menu__search-btn {
    margin-left: auto; }
  .nav-menu__wrapper {
    background: #ffffff; }
  .nav-menu .nav-list__wrapper {
    margin-top: 46px; }
  .nav-menu__footer {
    margin-top: auto;
    margin-bottom: 0; }
  .nav-menu__telegram {
    margin-top: 16px; }
  .nav-menu__link {
    font-family: "TT Lakes Medium", sans-serif;
    font-weight: 500; }
  .nav-menu__mail {
    transition: color 0.3s; }
    .nav-menu__mail:hover {
      color: #41F6D7; }
  .nav-menu__tel {
    font-size: 24px;
    line-height: 32px;
    font-family: "TT Lakes Bold", sans-serif;
    transition: color 0.3s; }
    .nav-menu__tel:hover {
      color: #41F6D7; }
  .nav-menu__number {
    margin-top: 12px; }
  .nav-menu__language {
    margin-top: 40px; }
  .nav-menu__language-link {
    font-family: "TT Lakes Medium", sans-serif;
    font-weight: 500;
    transition: color 0.3s; }
    .nav-menu__language-link:hover {
      color: #41F6D7; }
  .nav-menu__language-link + .nav-menu__language-link {
    margin-left: 6px; }
  .nav-menu__language-link--active {
    color: #7E7E82; }
  .nav-menu::-webkit-scrollbar {
    width: 0;
    height: 0; }
  .nav-menu .menu-logo__line--active {
    display: none; }

@media screen and (min-width: 360px) {
  .nav-menu {
    padding-left: 24px;
    padding-right: 24px; } }

@media screen and (max-width: 360px) {
  .nav-menu__footer {
    margin-top: 0; } }

@media screen and (min-width: 1366px) {
  .nav-menu {
    display: flex;
    position: static;
    overflow: hidden;
    box-shadow: none; } }

.nav-menu--active {
  display: block;
  background: #ffffff;
  position: fixed; }

.menu-logo {
  display: flex;
  align-items: center; }
  .menu-logo__inner {
    display: flex;
    align-items: center;
    position: relative;
    padding-left: 16px; }
  .menu-logo__line {
    width: 10px;
    height: 2px;
    background: #ffffff;
    position: relative;
    left: -4px; }
    .menu-logo__line:before {
      content: '';
      width: 18px;
      height: 2px;
      background: #ffffff;
      position: absolute;
      left: 0;
      top: -7px; }
    .menu-logo__line:after {
      content: '';
      width: 14px;
      height: 2px;
      background: #ffffff;
      position: absolute;
      left: 0;
      top: 7px; }
  .menu-logo__btn--close {
    z-index: 1; }
  .menu-logo__btn--close {
    position: relative; }
    .menu-logo__btn--close:before {
      content: url("data:image/svg+xml, %3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M2.79348 1.25712C2.40295 0.866593 1.76979 0.866592 1.37927 1.25712L1.25712 1.37927C0.866592 1.76979 0.866593 2.40296 1.25712 2.79348L7.46364 9L1.25712 15.2065C0.866593 15.597 0.866592 16.2302 1.25712 16.6207L1.37927 16.7429C1.76979 17.1334 2.40295 17.1334 2.79348 16.7429L9 10.5364L15.2065 16.7429C15.597 17.1334 16.2302 17.1334 16.6207 16.7429L16.7429 16.6207C17.1334 16.2302 17.1334 15.597 16.7429 15.2065L10.5364 9L16.7429 2.79348C17.1334 2.40296 17.1334 1.76979 16.7429 1.37927L16.6207 1.25712C16.2302 0.866592 15.597 0.866593 15.2065 1.25712L9 7.46364L2.79348 1.25712Z' fill='white'/%3E%3C/svg%3E");
      position: absolute;
      top: 10px; }
  .menu-logo__btn--open {
    position: relative; }
    .menu-logo__btn--open:before {
      content: url("data:image/svg+xml,%3Csvg width='18' height='16' viewBox='0 0 18 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0 1C0 0.447715 0.447715 0 1 0H17C17.5523 0 18 0.447715 18 1C18 1.55228 17.5523 2 17 2H1C0.447716 2 0 1.55228 0 1ZM0 8C0 7.44772 0.447715 7 1 7H9C9.55228 7 10 7.44772 10 8C10 8.55229 9.55229 9 9 9H1C0.447715 9 0 8.55229 0 8ZM1 14C0.447715 14 0 14.4477 0 15C0 15.5523 0.447716 16 1 16H13C13.5523 16 14 15.5523 14 15C14 14.4477 13.5523 14 13 14H1Z' fill='white'/%3E%3C/svg%3E%0A");
      position: absolute;
      top: 12px; }

@media screen and (min-width: 769px) {
  .menu-logo__btn {
    display: none; } }

.nav-list__item + .nav-list__item {
  margin-top: 28px; }

.nav-list__item {
  min-height: 32px; }

.nav-list__item {
  position: relative;
  padding-left: 24px;
  margin-left: -24px; }
  .nav-list__item:before {
    content: '';
    position: absolute;
    width: 4px;
    height: 32px;
    left: 0;
    top: -3px;
    background: #41F6D7;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    transition: all 0.4s;
    transform: scale(0); }
  .nav-list__item:hover:before {
    transform: scale(1); }
  .nav-list__item:hover {
    color: #7E7E82; }

.nav-list__item--active {
  color: #7E7E82; }
  .nav-list__item--active:after {
    content: '';
    position: absolute;
    width: 4px;
    height: 32px;
    left: 0px;
    top: -3px;
    background: #41F6D7;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px; }

@media screen and (min-width: 360px) {
  .nav-list__item--active {
    color: #7E7E82;
    position: relative;
    padding-left: 24px;
    margin-left: -24px; }
    .nav-list__item--active:before {
      content: '';
      position: absolute;
      width: 4px;
      height: 32px;
      left: 0px;
      top: -3px;
      background: #41F6D7;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px; } }
